/* Header Styles */
.header {
    height: 60px;
    display: flex;
    align-items: center;
    background-color: #131921;
    color: azure;
    position: sticky;
    top: 0;
    z-index: 100;
    padding: 0 20px;
}
.sections{
    margin-left: 400px;
}
/* Section Styles */
.section {
    background-color: #0c0f1300;
    color: azure;
    margin: 20px;
    font-family: "monospace";
    font-weight: bold;
    padding: 10px;
}

/* Media Queries */

@media screen and (max-width: 1212px) {
    .header {
        height: 90px;
        font-size: 16px;
    }

    .section {
        margin: 15px;
        font-size: 16px;
    }
    .sections{
        margin-left: 250px;
    }
}
/* Tablets (max-width: 1024px) */
@media screen and (max-width: 1068px) {
    .header {
        height: 90px;
        font-size: 16px;
    }

    .section {
        margin: 15px;
        font-size: 16px;
    }
    .sections{
        margin-left: 0px;
    }
}


/* Mobile Devices (max-width: 768px) */
@media screen and (max-width: 815px) {
    .header {
        height: 105px;
        font-size: 14px;
        padding: 0 10px;
        overflow-y: scroll;
    }

    .section {
        margin: 10px;
        font-size: 14px;
    }

    .sections{
        margin-left: 30px;
    }
}

/* Small Mobile Devices (max-width: 480px) */
@media screen and (max-width: 480px) {
    .header {
        height: 50px;
        font-size: 12px;
        padding: 0 5px;
    }

    .section {
        margin: 5px;
        font-size: 12px;
        padding: 5px;
    }
    .sections{
        margin-left: 10px;
    }
}
