.icon-container {
    position: relative;
    display: inline-block;
}

.tooltip {
    visibility: hidden;
    background-color: black;
    color: white;
    padding: 5px 10px;
    border-radius: 5px;
    position: absolute;
    bottom: 120%;
    left: 50%;
    transform: translateX(-50%);
    white-space: nowrap;
    font-size: 14px;
    opacity: 0;
    transition: opacity 0.3s;
}

.icon-container:hover .tooltip {
    visibility: visible;
    opacity: 1;
}

/* Default styles (for desktop/web) */
.icon-container {
    font-size: 40px;
    text-align: center;
    margin-top: 50px;
    display: flex;
    justify-content: center;
    gap: 20px;
}

/* For smaller screens (tablets and mobile) */
@media screen and (max-width: 1000px) {
    .icon-container {
        font-size: 30px; /* Reduce icon size */
        flex-wrap: wrap; /* Allow icons to wrap */
        gap: 10px;
    }

    h2 {
        font-size: 18px; /* Reduce heading size */
        text-align: center;
    }

    .tooltip {
        font-size: 12px; /* Adjust tooltip size */
    }

    .about-section {
        display: flex;
        gap: 100px;
        flex-direction: row; /* Stack content */
        text-align: center;
        margin-left: 0;
        padding: 20px;
    }

    .about-section img {
        width: 150px; /* Adjust image size */
        height: 150px;
        margin: auto;
        position: static;
    }

    .about-section div {
        width: 100%;
        margin: auto;
        font-size: 16px;
    }

    .skills-card {
        padding: 10px;
    }
}

/* For very small screens (phones) */
@media screen and (max-width: 480px) {
    .icon-container {
        font-size: 25px;
        flex-direction: column; /* Stack icons */
        align-items: center;
    }

    .tooltip {
        font-size: 10px;
    }

    .about-section img {
        width: 120px;
        height: 120px;
    }

    .about-section div {
        font-size: 14px;
    }
}
.about-para {
    text-align: left;
    width: 850px;
    margin-left: 400px;
    overflow-y: scroll;
    margin-top: 50px;
    font-family: monospace;
    font-style: italic;
    font-size: 20px;
}
.about-section {
    display: flex;
    flex-direction: row; /* Stack content */
    text-align: center;
    margin-left: 0;
    padding: 20px;
}
@media screen and (max-width: 1000px) {
    .about-para {
        width: 50%;
        margin-left: 20px;
        margin-right: 400px;
        font-size: 18px;
        overflow-y: auto;
    }
}